.hs-root {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 24px 14px;
  background: linear-gradient(180deg, #fff7ea 0%, #fff2de 100%);
}

.hs-phone-frame {
  position: relative;
  width: 300px;
  height: 620px;
  border-radius: 46px;
  background: #f2f2f3;
  border: 5px solid #cfd0d4;
  overflow: hidden;
  box-shadow: 0 24px 48px rgba(0, 0, 0, 0.18);
}

.hs-phone-frame::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 26px;
  background: #000;
  border-radius: 999px;
  z-index: 100;
}

.hs-page {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: radial-gradient(circle at 15% 10%, #ffeecf 0, #fff 40%), radial-gradient(circle at 90% 0, #dff4ff 0, #fff 35%);
}

.hs-status-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 20px 8px;
  font-size: 12px;
  font-weight: 700;
  color: #000;
  background: #f2f2f3;
  flex-shrink: 0;
}

.hs-battery {
  position: relative;
  width: 18px;
  height: 10px;
  border: 1.5px solid #111;
  border-radius: 2px;
  display: inline-flex;
  align-items: center;
  padding: 1px;
  box-sizing: border-box;
}

.hs-battery__level {
  width: 100%;
  height: 100%;
  background: #111;
  border-radius: 1px;
}

.hs-battery__cap {
  position: absolute;
  right: -3px;
  top: 2px;
  width: 2px;
  height: 4px;
  background: #111;
  border-radius: 1px;
}

.hs-content {
  position: relative;
  flex: 1;
  overflow-y: auto;
  padding: 12px;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.hs-content::-webkit-scrollbar {
  display: none;
}

.hs-bottom-nav {
  flex-shrink: 0;
  margin: 0 8px 8px;
  z-index: 10;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 4px;
  border-radius: 18px;
  border: 1px solid #e5e7eb;
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(10px);
  padding: 8px 6px 10px;
}

.hs-bottom-nav__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2px;
  border-radius: 12px;
  padding: 4px 2px;
  color: #52525b;
  text-decoration: none;
}

.hs-bottom-nav__item--active {
  background: #111827;
  color: #fff;
}

.hs-bottom-nav__icon {
  font-size: 14px;
  line-height: 1;
}

.hs-bottom-nav__label {
  font-size: 10px;
  font-weight: 700;
  line-height: 1;
}
